import react from 'react'
import axios from 'axios'
import Login from '../components/login'
import Router from 'next/router'



class Index extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            login: false
        }
    }

    request(){
        axios.get("http://localhost/TEST/index.php")
        .then((res) => console.log(res.data))
    }
    getData(){
        axios.get("http://localhost/TEST/getdata.php", {
            params:{
                username:'Admin'
            }
        })
        .then( (resp) => console.log(resp.data))
        

    }
    postdata(){
        axios({
            method: 'post',
            url: 'http://localhost/TEST/postdata.php',
            data: {
                username:'jingjo'
            },  
           // config: { headers: {'Content-Type': 'multipart/form-data' }}

          })
          .catch((error) => console.log(error))
        .then((resp)=> console.log(resp))
        console.log(resp.data)
        

    
    }
 
    render(){
        return(
            <div>
                <button onClick={this.request.bind(this)} >REQUEST</button>
                <button onClick={this.getData.bind(this)} > GET DATA </button>
                <button onClick={this.postdata.bind(this)} >POST AND RESPONSE</button>

                <Login />

            </div>
        )
    }
}

export default Index